import { ReactNode } from "react"
import { Link } from "react-router-dom"
import { Button, SxProps, Typography } from "@rp/react/web/mui/core"
import { ArrowForwardIos } from "@rp/react/web/mui/icons"

type Props = {
  to: string
  sx?: SxProps
  label: ReactNode
}

export const MobileArrowForwardButton = ({ to, label, sx }: Props) => {
  return (
    <Button
      fullWidth
      variant="text-outlined-mono"
      to={to}
      sx={{ textTransform: "none", py: 0, ...sx }}
      component={Link}
      disableRipple
    >
      <Typography sx={{ fontSize: 20, fontWeight: 700, flexGrow: 1 }}>{label}</Typography>
      <ArrowForwardIos sx={{ fontSize: 18 }} />
    </Button>
  )
}
